@import './../blocks/styles/mixins.scss';
@import './../blocks/styles/vars.scss';

.sliderItem {
   display: flex;
   flex-direction: column;

   text-align: center;

   @media screen and (min-width:1280px) {
      flex-direction: row;
   }

   .number {
      padding-top: 6px;

      // margin-right: 18px;
      margin: 0 auto 20px auto;

      width: 61px;
      height: 61px;

      background-color: $accentColor;

      border-radius: 50%;

      @include textEdit(700, 2.1875rem, 1.457);
      text-align: center;
      color: $primaryColor;

      @media screen and (min-width:1280px) {
         margin: 0;
         margin-right: 18px;
      }
   }


   .text {
      .title {
         padding-bottom: 10px;

         @include textEdit(500, 1.125rem, 1.5);
      }

      .subscr {
         padding-bottom: 19px;

         @include textEdit(400, 1.125rem, 1.44);
      }

      @media screen and (min-width:1280px) {
         text-align: left;
      }
   }
}