@import './../styles/mixins.scss';
@import './../styles/vars.scss';

.feedback {

   text-align: center;

   .conteiner {
      padding: 0 15px;

      @media screen and (min-width:576px) {
         padding: 0 35px;
      }

      @media screen and (min-width:768px) {
         padding: 0 88px 0 170px;
      }

      @media screen and (min-width:1280px) {
         padding: 0 580px 0 190px;
      }

      @media screen and (min-width:1440px) {
         padding: 0 660px 0 270px;
      }

      @media screen and (min-width:1920px) {
         padding: 0 900px 0 510px;
      }


      .title {
         padding-bottom: 15px;

         @include textEdit (400, 1.625rem, 1.5);

         text-align: center;

         span {
            display: none;
         }

         @media screen and (min-width:768px) {
            text-align: left;

            @include textEdit (500, 1.25rem, 1.45);

            transform: translate(-126px, 10px);

            span {
               display: inline;
            }
         }

         @media screen and (min-width:1280px) {
            font-size: 1.75rem;

         }

         @media screen and (min-width:1280px) {
            font-size: 1.25rem;

         }
      }

      .showMore {
         margin-top: 40px;

         display: inline-block;
         text-align: center;

         position: relative;

         @media screen and (min-width:1280px) {
            transform: translate(197px, 0px);
         }

         &::after {
            content: "";

            width: 6px;
            height: 11px;

            background: url('./../../../assets/img/icons/arrowRight.png');
            background-repeat: no-repeat;
            position: absolute;
            top: 8px;
            right: -14px;
         }

         .btn {

            @include textEdit (500, 1.06rem, 1.47);

            color: $linkColor;

         }
      }
   }
}