@import './../blocks/styles/mixins.scss';
@import './../blocks/styles/vars.scss';

.comment {
   margin-top: 35px;
   padding-bottom: 35px;

   border-bottom: 0.5px solid $secondaryColor;
   text-align: left;



   // grid-template-rows: 90px auto auto;
   // grid-template-columns: 1fr 1fr;
   // grid-template-areas:
   //     "title movie"
   //     "subscr movie"
   //     "feedback feedback";


   .person {
      padding-bottom: 60px;

      display: flex;
      align-items: center;

      position: relative;

      @media screen and (min-width:768px) {
         padding-bottom: 0px;
         margin-bottom: 26px;
      }

      .personImg {
         margin-right: 18px;


         @media screen and (min-width:768px) {
            margin: 0;

            position: absolute;
            top: -14px;
            left: -121px;
         }
      }

      .personWrap {
         .title {
            margin-bottom: 10px;

            @include textEdit(500, 1rem, 1.25);
         }

         .job {

            @include textEdit(500, 0.875rem, 1.5);
         }

         .stars {
            margin-top: 7px;

            ul {
               display: flex;

               li {
                  margin-right: 7px;
               }
            }
         }
      }

      .impresWrap {

         position: absolute;
         bottom: 14px;
         left: 20px;

         @media screen and (min-width:768px) {
            bottom: 1px;
            left: 188px;

         }

         .impress {


            // margin-left: 20px;

            @include textEdit(500, 0.875rem, 1.5);

            color: $secondaryColor;

            position: relative;

            &::before {
               content: "";

               width: 15px;
               height: 15px;

               background: url('./../../assets/img/icons/bird.png');

               position: absolute;
               top: 3px;
               left: -20px;

            }
         }
      }
   }

   .textBlock {
      .text {
         margin-bottom: 22px;

         @include textEdit(400, 1rem, 1.5);
      }

      .date {
         margin-top: 14px;

         @include textEdit(500, 0.875rem, 1.5);

         color: $secondaryColor;
      }
   }

}