@import './../blocks/styles/vars.scss';
@import './../blocks/styles/mixins.scss';

.reviews {





   display: flex;
   flex-direction: column;

   position: relative;

   @media screen and (min-width:576px) {
      margin-top: 20px;
      padding: 13px 0 8px 41px;

      flex-direction: row;
      align-items: center;


   }

   @media screen and (min-width:1280px) {
      width: 520px;

      padding: 12px 0 33px 41px;
   }

   @media screen and (min-width:1440px) {
      width: 562px;

      padding: 24px 0 33px 36px;
   }


   &::before {
      content: "";
      width: 36px;
      height: 41px;

      background-image: url('./../../assets/img/feedback/cup.svg');
      background-repeat: no-repeat;

      position: absolute;
      top: -3px;
      left: -48px;

      @media screen and (min-width:576px) {
         top: -13px;
      }

      @media screen and (min-width:768px) {
         top: -3px;
      }
   }

   .reviewsTxt {
      @include textEdit(400, 0.875rem, 1.428);

      @media screen and(min-width: 576px) {
         font-size: 1.132rem;
         line-height: 1.5;

      }

      @media screen and (min-width:768px) {
         transform: translate(1px, 4px);

      }
   }

   .stars {
      margin-top: 7px;

      display: flex;

      @media screen and (min-width:576px) {
         margin-left: 10px;
         transform: translate(-2px, 3px);
         -webkit-transform: translate(-2px, 3px);
         -moz-transform: translate(-2px, 3px);
         -ms-transform: translate(-2px, 3px);
         -o-transform: translate(-2px, 3px);
      }

      .star {
         margin-right: 7px;
      }
   }
}