@import './../styles/vars.scss';
@import './../styles/mixins.scss';

.map {
   margin-top: 45px;
   margin-bottom: 52px;




   .wrap {
      padding: 0;

      max-width: 1553px;

      margin: 0 auto;

      overflow: hidden;

      position: relative;
      // z-index: -1000;

      @media screen and (min-width:1440px) {
         min-width: 100%;
      }


      iframe {
         width: 100%;
         height: 423px;

         @media screen and (min-width:576px) {
            height: 509px;
         }
      }

      .adress {
         padding: 52px 15px 20px 13px;

         margin: 52px 23px 40px 23px;
         border: 0.5px solid $secondaryColor;

         @media screen and (min-width:576px) {
            margin: 52px 88px 40px 88px;

         }

         @media screen and (min-width:768px) {
            margin: 52px 184px 40px 184px;

            padding: 58px 0px 20px 30px;

         }

         @media screen and (min-width:768px) {
            width: 420px;
            height: 469px;


            position: absolute;
            top: -32px;
            left: -132px;

            background-color: $primaryColor;
         }

         @media screen and (min-width:1280px) {
            left: 24px;
            top: -40px;
         }

         @media screen and (min-width:1440px) {
            left: -36px;
         }


         .card {
            margin-top: 40px;

            display: flex;
            justify-content: center;

            .cardImg {
               margin-right: 11px;

               width: 67px;
               height: 67px;
            }

            .cardTxt {
               .cardName {
                  margin-bottom: 5px;

                  @include textEdit(500, 1rem, 1.25);
               }

               .cardJob {
                  margin-bottom: 7px;

                  @include textEdit(400, 0.875rem, 1.5);
               }

               .cardLink {
                  @include textEdit(500, 0.875rem, 1.5);

                  color: $linkColor;
               }
            }
         }
      }
   }
}