@import './../blocks/styles/mixins.scss';
@import './../blocks/styles/vars.scss';

.interval {
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex-wrap: wrap;

   padding-left: 20px;
   padding-bottom: 39px;

   border-bottom: 0.5px solid $secondaryColor;

   @media screen and (min-width:576px) {
      padding-bottom: 0;
   }

   .intervalTxt {
      .time {
         padding-top: 20px;
         padding-bottom: 10px;

         @include textEdit(400, 1.125rem, 1.44);
      }

      .price {
         padding-bottom: 10px;

         @include textEdit(600, 1.125rem, 1.44);

         display: none;

         @media screen and (min-width:576px) {
            display: block;

         }


      }

      .price2 {
         padding-bottom: 10px;

         @include textEdit(600, 1.125rem, 1.44);

         @media screen and (min-width:576px) {
            display: none;
         }
      }


      .place {
         padding-bottom: 20px;

         @include textEdit(400, 1.125rem, 1.0);
      }
   }

   .btn {
      padding: 16px 33px;
      margin-right: 20px;
      color: $accentColor;
      border: 1px solid $accentColor;
      transition: all .3s ease-in-out;

      &:hover {
         background-color: #43516b;
         color: $primaryColor;
      }

      &:focus {
         background-color: #55433b;
         color: $primaryColor;
      }
   }
}