@import './../styles/vars.scss';
@import './../styles/mixins.scss';

.about {

   margin-bottom: -7px;


   @media screen and (min-width:576px) {
      margin-bottom: 50px;
   }

   @media screen and (min-width:1280px) {
      margin-top: -33px;
   }

   @media screen and (min-width:1440px) {
      margin-top: -28px;
   }



   .conteiner {
      @extend %conteiner;


      @media screen and (min-width:1280px) {
         padding: 0 184px 0 71px;

      }

      @media screen and (min-width:1440px) {
         padding: 0px 257px 0 153px;

      }

      @media screen and (min-width:1920px) {
         padding: 0px 500px 0 392px;

      }

      .wrap {
         padding-bottom: 79px;

         display: flex;
         flex-direction: column;

         position: relative;



         @media screen and (min-width:576px) {
            border-bottom: 0.5px solid $secondaryColor;

         }


         @media screen and (min-width:1280px) {
            flex-direction: row-reverse;
            justify-content: space-between;


         }


         .descr {

            @media screen and (min-width:1280px) {
               max-width: 584px;



            }

            .descrItem {
               &:first-child {
                  margin-bottom: 22px;
               }

               .descrTitle {
                  padding-bottom: 24px;

                  @include textEdit(500, 1.125rem, 1.44);

                  @media screen and (min-width:576px) {
                     padding-bottom: 18px;

                     text-align: left;
                  }
               }

               .descrTxt {
                  @include textEdit(400, 1rem, 1.5);


               }

               .descrTxtFirst {
                  margin-bottom: 40px;
               }

               .innerBtn {
                  display: inline-block;

                  margin-top: 22px;

                  position: relative;

                  @media screen and (min-width:1280px) {
                     margin-top: 14px;
                  }


                  // &::after {
                  //    content: "";
                  //    width: 11px;
                  //    height: 6px;

                  //    background-image: url('./../../../assets/img/icons/arrow-bottom.svg');
                  //    background-repeat: no-repeat;

                  //    position: absolute;
                  //    top: 45%;
                  //    right: -18px;
                  // }

                  .btn {
                     @include textEdit(500, 1rem, 1.43);

                     color: $linkColor;

                     background-color: $primaryColor;
                  }
               }
            }
         }
      }
   }
}