@import './../../blocks/styles/mixins.scss';
@import './../../blocks/styles/vars.scss';

.schedule {
   .conteiner {
      @extend %conteiner;

      @media screen and (min-width:1280px) {
         padding: 0 440px 0 71px;

      }

      @media screen and (min-width:1440px) {
         padding: 0 262px 0 150px;

      }

      @media screen and (min-width:1920px) {
         padding: 0 760px 0 391px;

      }

      .title {
         padding-bottom: 37px;

         @include textEdit(500, 1.4rem, 1.45);

         text-align: center;

         @media screen and (min-width:576px) {
            padding-bottom: 50px;

            @include textEdit(500, 1.59rem, 1.45);

         }

         @media screen and (min-width:768px) {
            padding-bottom: 27px;

            font-size: 1.2rem;

            text-align: left;

         }

      }

      .wrap {
         padding: 20px 0 0 0px;



         display: grid;
         grid-template-columns: auto;
         grid-auto-flow: row;

         border: 0.5px solid $secondaryColor;

         .date {
            display: inline-block;

            padding-bottom: 20px;

            border-bottom: 0.5px solid $secondaryColor;

            @include textEdit(400, 1.125rem, 1.44);

            text-align: center;

            @media screen and (min-width:576px) {
               padding-left: 20px;
               padding-top: 6px;

               text-align: left;


            }

            span {
               position: relative;

               &::after {
                  content: "";

                  width: 35px;
                  height: 35px;

                  background-image: url('./../../../assets/img/icons/kalendar.svg');
                  background-repeat: no-repeat;
                  color: $secondaryColor;
                  position: absolute;
                  top: -6px;
                  right: -48px;

                  display: none;

                  @media screen and (min-width:576px) {
                     display: inline-block;
                  }
               }
            }


         }

         .reviews {
            padding: 32px 0 5px 68px;

            @media screen and (min-width:576px) {
               padding: 32px 0 13px 68px;
            }

            @media screen and (min-width:768px) {
               padding: 23px 0 25px 79px;
            }
         }
      }
   }
}