@import './../styles/vars.scss';
@import './../styles/mixins.scss';


footer {


   .top {
      .conteiner {

         @extend %conteiner;

         @media screen and (min-width:1280px) {
            padding: 0 140px 0 135px;

         }

         @media screen and (min-width:1440px) {
            padding: 0 219px 0 215px;

         }

         @media screen and (min-width:1920px) {
            padding: 0 456px 0 457px;

         }

         .topInner {
            margin-top: 87px;

            padding-bottom: 27px;

            display: flex;
            justify-content: space-between;

            border-bottom: 0.5px solid $secondaryColor;

            @media screen and (min-width:576px) {
               padding-bottom: 42px;
               border-bottom: 0;

            }


            .topRight {
               .social {
                  display: flex;
                  align-items: center;

                  .socialItem {
                     margin-right: 30px;

                     &:last-child {
                        margin-right: 0;
                     }

                     .socialLangList {
                        margin-right: 10px;

                        position: relative;

                        display: none;

                        @media screen and (min-width:1280px) {
                           display: block;
                        }

                        &::after {
                           content: "";

                           width: 11px;
                           height: 8px;

                           background: url('./../../../assets/img/icons/arrow-bottom.svg');
                           background-repeat: no-repeat;

                           position: absolute;
                           top: 6px;
                           right: -15px;
                        }

                        select {


                           border-style: none;
                           outline: 0;

                           @include textEdit(400, 0.9375rem, 1.2);

                           text-transform: uppercase;

                           -moz-appearance: none;
                           -webkit-appearance: none;
                           appearance: none;

                           option {
                              font-weight: normal;
                              font-size: 15px;
                              line-height: 18px;
                           }
                        }


                     }
                  }
               }
            }
         }
      }
   }

   .bottom {

      @media screen and(min-width:576px) {
         background-color: #FAF9F8;
      }

      .conteiner {
         @media screen and (min-width:1440px) {
            padding: 0 0 0 215px;

         }

         @media screen and (min-width:1920px) {
            padding: 0 0 0 457px;

         }

         .bottomInner {
            padding: 26px 0 90px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            position: relative;

            @media screen and(min-width:576px) {
               padding: 17px 0;
            }

            @media screen and (min-width:1280px) {

               justify-content: start;

            }

            .cardList {
               display: flex;

               @media screen and (min-width:1280px) {
                  margin-right: 276px;
               }

               .cardItem {
                  margin-right: 17px;




               }
            }

            .text {
               position: absolute;
               top: 73px;
               left: 10px;

               @include textEdit (400, 0.875rem, 1.214);
               text-align: center;

               color: $secondaryColor;

               @media screen and(min-width:576px) {
                  position: static;
               }

               .pg1 {
                  padding-bottom: 10px;
               }
            }

            .langList {
               margin-right: 10px;

               position: relative;

               @media screen and (min-width:1280px) {
                  display: none;
               }

               &::after {
                  content: "";

                  width: 11px;
                  height: 8px;

                  background: url('./../../../assets/img/icons/arrow-bottom.svg');
                  background-repeat: no-repeat;

                  position: absolute;
                  top: 6px;
                  right: -15px;
               }

               select {


                  border-style: none;
                  outline: 0;

                  @include textEdit(400, 0.9375rem, 1.2);

                  text-transform: uppercase;

                  -moz-appearance: none;
                  -webkit-appearance: none;
                  appearance: none;

                  option {
                     font-weight: normal;
                     font-size: 15px;
                     line-height: 18px;
                  }
               }
            }
         }

      }
   }
}